import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { environment } from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})

/**
 * The StoreService abstracts out storing and retrieving local configuration and settings
 *
 * Mapping of keys:
 * last_user_env = <user>:<env> : stores the last login email address and environment
 * max_user_env_index = <max_index> : stores the total number of user/env combinations in the Store
 * <user>:<env> = <index> : user/env index which is used to lookup/store specific settings for that combination
 * <index>:<key> = <value> : user/env environment specific storage of value on key
 *
 * Example:
 * last_user_env_index = 1
 * max_user_env_index = 1
 * jj@nra.com:prod = 1 // JJ in production
 * 1:user_env = "jj@nra.com:prod" // JJ in production
 * 1:language = "US_en"
 * 1:token = "Bearer XDFJKLSJ"
 * 1:fingerprint = true
 *
 */
export class StoreService {
  // Locally cached values for optimization
  private userenvindex: number;
  private user: string;
  private env: string;

  constructor(private storage: Storage) { }

  /**
   * Returns the current user for which StoreStorage is configured
   * @return A promise to the user email.
   */
  async getUser(): Promise<string> {
    if (!this.user) {
      const userenv = await this.getUserEnv();
      if (userenv) {
        this.user = userenv[0];
      } else {
        this.user = null;
      }
    }
    return this.user;
  }

  /**
   * Returns the current environment which StoreStorage is using
   * @return A promise to the environment name.
   */
  async getEnv(): Promise<string> {
    if (!this.env) {
      const userenv = await this.getUserEnv();
      if (userenv) {
        this.env = userenv[1];
      } else {
        this.env = environment.appEnv;
      }
    }
    return this.env;
  }

  /**
   * Returns the current user and environment which StoreStorage is using
   * @return A promise to an array containing the user and environment
   */
  async getUserEnv(): Promise<[string, string]> {
    const userenv = await this.get('user_env');
    if (userenv) {
      return userenv.split(':');
    } else {
      return null;
    }
  }

  /**
   * Switch StoreStorage to use a storage space for the specified user and environment
   * @param user - user's email address
   * @param env - environment indicator. E.g. 'p', 's', 'v', 'd' for production, staging, preview and development
   * @return A promise to the environment's internal index
   */
  async switchUserEnv(user: string, env: string): Promise<any> {
    const userenv =  user + ':' + env;
    this.userenvindex = await this.storage.get(userenv);
    if (!this.userenvindex) {
      this.userenvindex = (await this.storage.get('max_user_env_index')) || 0;
      this.userenvindex++;
      await this.storage.set('max_user_env_index', this.userenvindex);
      await this.storage.set(userenv, this.userenvindex);
      await this.set('user_env', userenv);
    }
    // Wiping local user/env cache
    this.user = null;
    this.env = null;
    return this.storage.set('last_user_env_index', this.userenvindex)
      .then( () => this.userenvindex);
  }

  /**
   * Store value under key in the current user's environment
   */
  async set(key: string, value: any): Promise<any> {
    return this.storage.set(await this.getEnvKey(key), value);
  }

  /**
   * Retrieve value for key in the current user's environment
   */
  async get(key: string): Promise<any> {
    return this.storage.get(await this.getEnvKey(key));
  }

  /**
   * Remove key and value under the current user's environment
   */
  async remove(key: string): Promise<any> {
    return this.storage.remove(await this.getEnvKey(key));
  }

  /**
   * Get internal, environment specific storage key for the specified key
   */
  private async getEnvKey(key: string) {
    if (!this.userenvindex) {
      this.userenvindex = await this.storage.get('last_user_env_index');
    }
    return this.userenvindex + ':' + key;
  }
}