import { Component, OnInit, Input } from '@angular/core';
import { CaseService } from 'src/app/services/case.service';
import { FormBuilder, FormGroup, Validators, AbstractControl, ReactiveFormsModule } from '@angular/forms';
import { Storage } from '@ionic/storage';
import { slideConf } from './slide-options';
import { Platform } from '@ionic/angular';

@Component({
  selector: 'app-coin-view',
  templateUrl: './coin-view.component.html',
  styleUrls: ['./coin-view.component.scss'],
})
export class CoinViewComponent implements OnInit {
  @Input() hash;
  @Input() data;
  @Input() coinScore;
  @Input() transactions;
  @Input() owner;
  @Input() walletId;
  @Input() coinType;
  @Input() writableCaseId;
  @Input() writableCases;
  @Input() availableCases;
  @Input() countryFlag;
  @Input() countryFullName;
  traceHistory: any;
  private scoreLegend = {
    0: 'No attribution exists for this address, the address has no transactions, or this is a new address for which CipherTrace is in the process of calculating its risk classification (in this situation, the updated risk classification should appear within a few hours).',
    1: `No transactions with any level 9 or 10 addresses within one hop.`,
    2: 'This address is controlled by a trusted exchange that has not been labelled High Risk Exchange and that has transacted with level 9 or 10 addresses one or more times within one hop.',
    4: `This address has transacted with a level 9 address once within one hop. Does not propagate further.`,
    5: 'This address has transacted with a level 10 address once within one hop.  Does not propagate further.',
    8: 'This address has transacted with level 9 addresses two or more times, or with a level 10 address once and level 9 addresses one or more times, within one hop. Does not propagate further.',
    9: 'This address has transacted with level 10 addresses two or more times within one hop.',
    10: 'Attribution data exists for this address that attributes it to a sanctioned entity or labels it as Criminal, Dark Market, Gambling, Malware, Ransomware, or Mixer.'
};
  private sanctionsScoreLegend = {
    9: 'Interacted with a sanctioned address more than once',
    10: 'This is a sanctioned address'
  }

  coinScoreDecorateTop: string = 'Risk';

  addToCaseFormGroup: FormGroup;
  caseId: AbstractControl;

  private slideOpts = slideConf;

  constructor(
    private caseService: CaseService,
    private formBuilder: FormBuilder,
    private storage: Storage,
    private platform: Platform
  ) {
    this.addToCaseFormGroup = formBuilder.group({
      caseId: ['', Validators.required]
    });
    this.caseId = this.addToCaseFormGroup.controls['caseId'];
  }

  ngOnInit() {
    this.coinScoreDecorateTop = this.decorateText(this.coinScoreDecorateTop);

    let newVal = [];

    this.storage.get('trace_history').then((val) => {
      if(val) {
        newVal = val.slice(0, 99);
      }

      // newVal[newVal.length++] = JSON.stringify({ hash: this.hash, coinType: this.coinType, score: this.coinScore });

      // TODO [THIS IS A HACK] need to figure out how to await api end point to return score
      // NOTE: logic here is broken with .push. need to use unshift like on ip.page.ts todo in ENG-815
      setTimeout(() => {
        newVal.push({ hash: this.hash, coinType: this.coinType, score: this.coinScore.risk });
        this.storage.set('trace_history', newVal);
      }, 6000)
    });
  }

  decorateText(text: string) {
    let output = '';
    for (let i = 0; i < text.length; i++) {
      output += `<span class="char">${text[i]}</span>`
    }
    return output;
  }
}
