import { Injectable } from '@angular/core';
import { CtbaseService } from '../api/ctbase.service';
import { Observable } from 'rxjs';
import { xhrOpts } from './options';
import { Case } from '../models/case';

@Injectable({
  providedIn: 'root'
})
export class CaseService {
  private xhrOptions = xhrOpts;

  constructor(
    private ctbase: CtbaseService
  ) { }

  fetchCases(): Observable<[Case]> {
    const url = `cases`;
    return this.ctbase.get<[Case]>(null, url, undefined, this.xhrOptions);
  }

  fetchCase(id: string): Observable<Case> {
    const url = `cases/${id}`;
    return this.ctbase.get<Case>(null, url, undefined, this.xhrOptions);
  }

  addCase(data) {
    const url = `cases`;

    // TODO: more work needed on data here
    // 1. created date, email, userid shouldn't be hardcoded
    // 2. updated date shouldn't be set to server
    const opts = {
      'addSavesEnabled': true,
      'createdDate': Date.now(),
      'creatorEmail': data.email,
      'creatorUserId': data.userId,
      'isOwner': true,
      'saves': [],
      'shares': [],
      'status': 'OPEN',
      'updatedDate': Date.now(),
      'writeEnabled': true,
      'name': data.name,
      'trxNotes': data.addressNote,
      'notes': data.caseNote || '',
      'number': data.number  || ''
    }

    return this.ctbase.post<{}>(null, url, undefined, opts);
  }

  addToCase(id: string, notes: string, hash: string, coinType): Observable<{}> {
    const url = `cases/${id}/saves`;

    let opts = {
      'name': '',
      'number': '',
      'notes': notes,
      'data': '{}',
      'saveType': coinType + '_addr',
      'address': hash,
      'date': Date.now()
    }

    // when adding coin to case, response we get is a string whereas expected
    // response is json, this causes error and retry, which adds coin to case
    // four times, using optional parameter to specify what response is
    return this.ctbase.post<{}>(null, url, undefined, opts, {responseType: 'string'});
  }

}
