import { Injectable } from '@angular/core';
import { CtbaseService } from '../api/ctbase.service';
import { Observable } from 'rxjs';
import { xhrOpts } from './options';

@Injectable({
  providedIn: 'root'
})
export class ShareService {
  private xhrOptions = xhrOpts;

  constructor(
    private ctbase: CtbaseService
  ) { }

  shareCase(caseId: string, email: string): Observable<{}> {
    const url = `cases/${caseId}/shares`;
    const opts = {
      'addSavesEnabled': false,
      'email': email,
      'writeEnabled': false
    }

    return this.ctbase.post<{}>(null, url, undefined, opts, {responseType: 'string'});
  }

  // Post share call to get details about person with whom case was shared
  getCaseShareMetadata(caseId: string) {
    const url = `cases/reload/${caseId}/shares`;
    return this.ctbase.get<{}>(null, url, undefined, this.xhrOptions);
  }

  unshareCase(caseId: string, email: string) {
    const url=`cases/${caseId}/shares/${email}`;

    // TODO: create DELETE method in ctbase.service.ts
  }
}
