import { Component, OnInit, Input } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { ModalController } from '@ionic/angular';
import { NewCaseComponent } from '../../components/new-case/new-case.component';
import { AddToExistingCaseComponent } from '../../components/add-to-existing-case/add-to-existing-case.component';
import _has from 'lodash/get';

@Component({
  selector: 'app-address-add',
  templateUrl: './address-add.component.html',
  styleUrls: ['./address-add.component.scss'],
})
export class AddressAddComponent implements OnInit {
  @Input() address: string;
  @Input() coinType: string;

  constructor(
    private popoverController: PopoverController,
    public modalController: ModalController
  ) { }

  ngOnInit() {}

  cancelAddDialog = success => this.popoverController.dismiss(success);

  async existingCaseModal() {
    const modal = await this.modalController.create({
      component: AddToExistingCaseComponent,
      componentProps: {
        address: this.address,
        addressType: this.coinType
      }
    });

    modal.onDidDismiss().then(data => {


console.log('address-add.component.ts onDidDismiss()');



      if(_has(data, 'data') && data.data.addressAdded) {
        this.cancelAddDialog(data.data.addressAdded);
      }
    });

    return await modal.present();
  }

  async newCaseModal() {
    const modal = await this.modalController.create({
      component: NewCaseComponent,
      componentProps: {
        address: this.address,
        addressType: this.coinType
      }
    });

    modal.onDidDismiss().then(data => {
      if(_has(data, 'data') && data.data.addressAdded) {
        this.cancelAddDialog(data.data.addressAdded);
      }
    });

    return await modal.present();
  }

}
