import { Component, OnInit, Input } from '@angular/core';
import { NavParams, PopoverController } from '@ionic/angular';
import { FormBuilder, FormGroup, Validators, AbstractControl, ReactiveFormsModule } from '@angular/forms';
import { ShareService } from 'src/app/services/share.service';
import { ToastController } from '@ionic/angular';

@Component({
  selector: 'app-share-via-email',
  templateUrl: './share-via-email.component.html',
  styleUrls: ['./share-via-email.component.scss'],
})
export class ShareViaEmailComponent implements OnInit {
  @Input() caseId: string;
  popover: PopoverController;

  shareCaseFormGroup: FormGroup;
  email: AbstractControl;

  constructor(
    navParams: NavParams,
    private shareService: ShareService,
    private formBuilder: FormBuilder,
    public toastController: ToastController
    ) {
    this.popover = navParams.get('popoverController');
    this.shareCaseFormGroup = formBuilder.group({
      email: ['', Validators.compose(
          [Validators.required, Validators.email]
        )
      ]
    });

    this.email = this.shareCaseFormGroup.controls['email'];
  }

  dismissPopover = () => this.popover.dismiss(this);

  onSubmit() {
    this.shareService.shareCase(this.caseId, this.email.value).toPromise().then(res => {
      this.dismissPopover();
      this.presentToast();
      this.shareCaseFormGroup.reset();
      return res;
    })
  }

  async presentToast() {
    const toast = await this.toastController.create({
      message: `You successfully shared this case with ${this.email.value}.`,
      duration: 3000
    });
    toast.present();
  }

  ngOnInit() {}

}
