import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { AuthGuard } from './guards/auth.guard';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'search',
    pathMatch: 'full'
  },
  // {
  //   path: 'home',
  //   loadChildren: './home/home.module#HomePageModule'
  // },
  // {
  //   path: 'list',
  //   loadChildren: './list/list.module#ListPageModule'
  // },
  { path: 'login', loadChildren: './login/login.module#LoginPageModule' },
  { path: 'search', canActivate: [AuthGuard], loadChildren: './search/search.module#SearchPageModule' },
  { path: 'result', loadChildren: './result/result.module#ResultPageModule' },
  { path: 'share', loadChildren: './share/share.module#SharePageModule' },
  { path: 'settings', loadChildren: './settings/settings.module#SettingsPageModule' },
  { path: 'appinfo', loadChildren: './appinfo/appinfo.module#AppinfoPageModule' },
  {
    path: 'trace',
    children: [
      {
        path: '',
        canActivate: [AuthGuard],
        loadChildren: './search/search.module#SearchPageModule'
      },
      {
        path: 'btc/:hash',
        canActivate: [AuthGuard],
        loadChildren: './trace/coin/btc/btc.module#BtcPageModule'
      },
      {
        path: 'eth/:hash',
        canActivate: [AuthGuard],
        loadChildren: './trace/coin/eth/eth.module#EthPageModule'
      },
      {
        path: 'bch/:hash',
        canActivate: [AuthGuard],
        loadChildren: './trace/coin/bch/bch.module#BchPageModule'
      },
      {
        path: 'ltc/:hash',
        canActivate: [AuthGuard],
        loadChildren: './trace/coin/ltc/ltc.module#LtcPageModule'
      },
      {
        path: 'transaction/:hash',
        canActivate: [AuthGuard],
        loadChildren: './trace/transactions/btc/btc.module#BtcPageModule'
      },
      {
        path: 'ip/:ip',
        canActivate: [AuthGuard],
        loadChildren: './trace/ip/ip.module#IpPageModule'
      }
    ]
  },
  {
    path: 'cases',
    children: [
      {
        path: '',
        canActivate: [AuthGuard],
        loadChildren: './cases/cases.module#CasesPageModule'
      },
      {
        path: ':hash',
        canActivate: [AuthGuard],
        loadChildren: './cases/case/case.module#CasePageModule'
      }
    ]
  },
  // { path: 'case', loadChildren: './cases/case/case.module#CasePageModule' },


  // { path: 'cases', loadChildren: './cases/cases.module#CasesPageModule' },
  // { path: 'eth', loadChildren: './trace/coin/eth/eth.module#EthPageModule' },
  // { path: 'btc', loadChildren: './trace/transaction/btc/btc.module#BtcPageModule' },
];

@NgModule({
  imports: [
    // RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
    // to solve a problem of url with params getting 404 on refresh
    // for production we can undo this
    // https://medium.com/wineofbits/angular-2-routing-404-page-not-found-on-refresh-a9a0f5786268
    RouterModule.forRoot(routes, { useHash: true })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule {}
