import {Injectable} from '@angular/core';
import {CtbaseService} from './ctbase.service';
import {from, Observable} from 'rxjs';
import {catchError, retry, switchMap} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  constructor(private ctbase: CtbaseService) {
  }

  login(username: string, password: string): Observable<{}> {
    const options = {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
        Accept: 'application/json',
      },
      withCredentials: true
    };
    /// TODO: Will need to rewrite the following using httpForms classes to get the proper URL encodings
    const body = `username=${username}&password=${password}`;
    return this.ctbase.post<{}>('auth', 'login', null, body, options);
  }

  logout(): Observable<{}> {
    const options = {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
        Accept: 'application/json'
      },
      withCredentials: true
    };

    return this.ctbase.post<{}>(null, 'logout', undefined, options);
  }
}
