import { Component, OnInit, Input } from '@angular/core';
import { CaseService } from '../../services/case.service';
import { PopoverController } from '@ionic/angular';
import { ModalController } from '@ionic/angular';
import { NewCaseComponent } from '../../components/new-case/new-case.component';
import _has from 'lodash/get';

@Component({
  selector: 'app-add-to-existing-case',
  templateUrl: './add-to-existing-case.component.html',
  styleUrls: ['./add-to-existing-case.component.scss'],
})
export class AddToExistingCaseComponent implements OnInit {
  myCases: any;
  mySearchableCases: any;
  addressAdded: boolean;
  @Input() address: string;
  @Input() addressType: string;

  constructor(
    private caseService: CaseService,
    private modalController: ModalController,
    private popoverController: PopoverController,
  ) { }

  ngOnInit() {
    this.fetchCases();
  }

  dismissModal = data => this.modalController.dismiss(this);

  cancelAddDialog = success => {
    this.popoverController.dismiss(success);
    this.modalController.dismiss(success);
  };

  onSubmit(caseId, caseName, notes, caseNumber, addressType) {
    const data = {
      caseId,
      caseName,
      notes,
      caseNumber,
      addressType
    };

    this.existingCaseModal(data);
  }

  async existingCaseModal(data) {
    const modal = await this.modalController.create({
      component: NewCaseComponent,
      componentProps: {
        address: this.address,
        addressType: this.addressType,
        existingCaseData: data
      }
    });

    modal.onDidDismiss().then(data => {
      if(_has(data, 'data') && data.data.addressAdded) {
        this.cancelAddDialog(data.data.addressAdded);

        console.log(this);
      }
    });

    return await modal.present();
  }

  searchCases(e) {
    const searchString = e.target.value.toLowerCase();

    this.mySearchableCases = this.myCases.filter(el => {
        return el.name.toLowerCase().indexOf(searchString) >= 0;
    })
  }

  fetchCases() {
    this.caseService.fetchCases().toPromise().then(res => {
      this.mySearchableCases = this.myCases = res
        .filter(item => item.writeEnabled === true && item.status === 'OPEN')
        .sort((a, b) => (a.updatedDate < b.updatedDate) ? 1 : -1);
    });
  }
}
