import {NgModule} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';
import {RouteReuseStrategy} from '@angular/router';

import {IonicModule, IonicRouteStrategy} from '@ionic/angular';
import {SplashScreen} from '@ionic-native/splash-screen/ngx';
import {StatusBar} from '@ionic-native/status-bar/ngx';

import { HammerGestureConfig, HAMMER_GESTURE_CONFIG } from '@angular/platform-browser';
import * as Hammer from 'hammerjs';

import {AppComponent} from './app.component';
import {AppRoutingModule} from './app-routing.module';
import {IonicStorageModule} from '@ionic/storage';

import { UserStatusComponent } from './components/user-status/user-status.component';

// https://ionicframework.com/docs/native/http/#installation
// https://github.com/sneas/ionic-native-http-connection-backend
// import {HttpClientModule} from '@angular/common/http'; // https://github.com/sneas/ionic-native-http-connection-backend

import { HttpBackend, HttpXhrBackend } from '@angular/common/http';
import { NativeHttpModule, NativeHttpBackend, NativeHttpFallback } from 'ionic-native-http-connection-backend';
import { Platform } from '@ionic/angular';

import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { SharedModule } from './components/shared-module';

import { ShareViaEmailComponent } from './components/share-via-email/share-via-email.component';
import { SharedWithInfoComponent } from './components/shared-with-info/shared-with-info.component';
import { AddressAddComponent } from './components/address-add/address-add.component';
import { NewCaseComponent } from './components/new-case/new-case.component';
import { AddToExistingCaseComponent } from './components/add-to-existing-case/add-to-existing-case.component';
import { ComponentsModuleModule } from './components-module/components-module.module';

import { AppVersion } from '@ionic-native/app-version/ngx';

export class CustomHammerConfig extends HammerGestureConfig {
  overrides = {
    'press': {time: 1000}
  };
}

@NgModule({
  declarations: [
    AppComponent,
    UserStatusComponent,
    ShareViaEmailComponent,
    SharedWithInfoComponent,
    AddressAddComponent,
    NewCaseComponent,
    AddToExistingCaseComponent
  ],
  entryComponents: [
    ShareViaEmailComponent,
    SharedWithInfoComponent,
    AddressAddComponent,
    NewCaseComponent,
    AddToExistingCaseComponent],
  imports: [
    BrowserModule,
    IonicModule.forRoot(),
    AppRoutingModule,
    IonicStorageModule.forRoot(),
    // HttpClientModule,
    NativeHttpModule,
    FormsModule,
    ReactiveFormsModule,
    SharedModule
  ],
  providers: [
    AppVersion,
    StatusBar,
    SplashScreen,
    {provide: RouteReuseStrategy, useClass: IonicRouteStrategy},
    {provide: HttpBackend, useClass: NativeHttpFallback, deps: [Platform, NativeHttpBackend, HttpXhrBackend]},
    {provide: HAMMER_GESTURE_CONFIG, useClass: CustomHammerConfig}
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
}
