import { Component, OnInit, Input } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl, ReactiveFormsModule } from '@angular/forms';
import { Router } from '@angular/router';
import { UserService } from 'src/app/services/user.service';
import { CaseService } from 'src/app/services/case.service';
import { Components } from '@ionic/core';
import { ModalController } from '@ionic/angular';
import { Case } from '../../models/case';
import _has from 'lodash/get';

@Component({
  selector: 'app-new-case',
  templateUrl: './new-case.component.html',
  styleUrls: ['./new-case.component.scss'],
})
export class NewCaseComponent implements OnInit {
  @Input() address: string;
  @Input() addressType: string;

  existingCase: boolean = false;

  addressAdded: boolean;
  buttonIcon: string;
  buttonText: string;
  email: string;
  userId: string;

  returnedData: Case;

  newCaseFormGroup: FormGroup;

  name: AbstractControl;
  number: AbstractControl;
  notes: AbstractControl;
  caseNote: AbstractControl;
  addressNote: AbstractControl;
  viewCtrl: any;

  constructor(
    private modalController: ModalController,
    private formBuilder: FormBuilder,
    private router: Router,
    private userService: UserService,
    private caseService: CaseService,

  ) {
    this.newCaseFormGroup = formBuilder.group({
      name: ['', Validators.required],
      number: [],
      caseNote: [],
      addressNote: []
    });

    this.name = this.newCaseFormGroup.controls['name'];
    this.number = this.newCaseFormGroup.controls['number'];
    this.caseNote = this.newCaseFormGroup.controls['caseNote'];
    this.addressNote = this.newCaseFormGroup.controls['addressNote'];
  }

  dismissModal = data => this.modalController.dismiss(this);

  onSubmit() {
    // disble form to prevent double submissions
    this.newCaseFormGroup.disable();

    if(!this.existingCase) {
      this.handleNewCase();
    } else {
      this.handleExistingCase();
    }
  }

  handleExistingCase() {
    this.userService.getUserData().toPromise().then(res => {
      this.email = res['email'];
      this.userId = res['userId'];

      this.caseService.addToCase(
        this['existingCaseData'].caseId,
        this.addressNote.value || '',
        this.address,
        this.addressType
        )
        .toPromise().then(res => {
          if(res) {
            this.addressAdded = true;
            this.dismissModal(true);
          }
        }
      );
    });
  }

  handleNewCase() {
    this.userService.getUserData().toPromise().then(res => {
      this.email = res['email'];
      this.userId = res['userId'];

      const data = {
        name: this.name.value,
        number: this.number.value || '', // API will return error if this is undefined
        caseNote: this.caseNote.value || '', // same here
        addressNote: this.addressNote.value || '',
        email: res['email'],
        userId: res['userId']
      };

      this.caseService.addCase(data).toPromise().then(res => {
        this.returnedData = res as Case;

        // If address and addressType exist, that means these were passed
        // adding address to case while creating a new case at the same time
        if(this.address && this.addressType) {
          this.caseService.addToCase(this.returnedData.caseId, this.addressNote.value || '', this.address, this.addressType)
            .toPromise().then(res => {
              this.addressAdded = true;
              this.dismissModal(true);
            }
          )
        } else {
          this.dismissModal(undefined);
        }
      }).catch(err => {
        console.error(`Error: ${err}`);
      });
    });
  }

  ngOnInit() {
    if(_has(this, 'existingCaseData')) {
      this.existingCase = true;

      // console.log('detected EXISTING CASE operation', this['existingCaseData']);
      this.newCaseFormGroup.controls['name'].setValue(this['existingCaseData'].caseName);
      this.newCaseFormGroup.controls['caseNote'].setValue(this['existingCaseData'].notes || '');
      this.newCaseFormGroup.controls['number'].setValue(this['existingCaseData'].caseNumber || '');

      this.newCaseFormGroup.get('name').disable({ onlySelf: true });
      this.newCaseFormGroup.get('caseNote').disable({ onlySelf: true });
      this.newCaseFormGroup.get('number').disable({ onlySelf: true });
    }
  }

}
