import { Injectable } from '@angular/core';
import { CtbaseService } from '../api/ctbase.service';
import { Observable } from 'rxjs';
import { xhrOpts } from './options';
import { Blockchain } from '../models/blockchain';

@Injectable({
  providedIn: 'root'
})
export class BlockchainService {
  private xhrOptions = xhrOpts;

  constructor(
    private ctbase: CtbaseService
  ) { }

  getData(): Observable<Blockchain> {
    const url = `blockchain`;
    return this.ctbase.get<Blockchain>(null, url, undefined, this.xhrOptions);
  }
}
