import {Platform, AlertController} from '@ionic/angular';
import {Injectable} from '@angular/core';
import {Storage} from '@ionic/storage';
import {BehaviorSubject} from 'rxjs';
import { AuthService } from '../api/auth.service';

const TOKEN_KEY = 'auth-token';

@Injectable({
  providedIn: 'root'
})
export class AuthenticationService {

  authenticationState = new BehaviorSubject(false);

  constructor(
    private storage: Storage,
    private plt: Platform,
    private api: AuthService,
    private alertCtrl: AlertController
    ) {
    this.plt.ready().then(() => {
      this.checkToken();
    });
  }

  checkToken() {
    return this.storage.get(TOKEN_KEY).then(res => {
      if (res) {
        this.authenticationState.next(true);
      } else {
        this.authenticationState.next(false);
      }
    });
  }

  authenticationStateReady() {
    return this.checkToken().then(() => {
      return this.authenticationState;
    });
  }

  login(username: string, password: string) {
    return this.api.login(username, password).toPromise()
      .then((a) => {
        console.log(a, document.cookie);
        // TODO, need to get XSRF-TOKEN from response instead of hardcoded
        return this.storage.set(TOKEN_KEY, 'Bearer 1234567').then(() => {
          this.authenticationState.next(true);
        });
      })
      .catch(err => {
        this.alertCtrl.create({
          header: 'Login failure',
          message: err,
          buttons: [
            {
              text: 'Ok',
              role: 'Dismiss',
            },
          ]
        }).then(alertEl => {
          alertEl.present();
        });
      });
  }

  logout() {
    return this.api.logout().toPromise()
      .then(() => {
        return this.storage.remove(TOKEN_KEY).then(() => {
          this.authenticationState.next(false);
        });
      });
  }

  isAuthenticated() {
    return this.authenticationState.value;
  }
}
