import { Injectable } from '@angular/core';
import { CtbaseService } from '../api/ctbase.service';
import { CoinScore } from '../models/coin-score';
import { LegacyTransactions } from '../models/legacy-transactions';
import { Transactions } from '../models/transactions';
import { Ip } from '../models/ip';
import { Observable } from 'rxjs';
import { xhrOpts } from './options';

@Injectable({
  providedIn: 'root'
})
export class TraceService {
  private xhrOptions = xhrOpts;

  constructor(
    private ctbase: CtbaseService
  ) { }

  searchHash(hash, types: string = 'all') {
    const url = `search?q=${hash}&types=${types}`;
    return this.ctbase.get<{}>(null, url, undefined, this.xhrOptions);
  }

  fetchCoinScore(hash: string, coin: string): Observable<CoinScore> {
    const url = `coin/score/${coin}/${hash}`;
    return this.ctbase.get<CoinScore>(null, url, undefined, this.xhrOptions);
  }

  fetchCoinTransactions(hash: string, coin: string): Observable<Transactions> {
    const url = `coin/${coin}/${hash}/transactions`;
    return this.ctbase.get<Transactions>(null, url, undefined, this.xhrOptions);
  }

  fetchCoinLegacyTransactions(hash: string, coin: string): Observable<LegacyTransactions> {
    const url = `coin/${coin}/transactions`;
    const opts = {
      "entityAddressBoolean": "Addresses",
      "visualizationRange": {
        "startDate": 1230969600,
        "endDate": (Date.now() / 1000)
      },
      "entityControlledAddresses": [
        {
          "address": hash
        }
      ],
      "associatedAddresses": [

      ],
      "pageSize": 100,
      "pageIndex": 1,
      "descSortOrder": true
    }

    return this.ctbase.post<LegacyTransactions>(null, url, undefined, opts);
  }

  fetchTransaction(hash) {
    const body = `query/graph/transactions?&type=tx_hash&address=${hash}`;
    return this.ctbase.get<{}>(null, body, undefined, this.xhrOptions);
  }

  fetchIp(ip: string, addressOffset: number) {
    const url = `ip/${ip}?offset=${addressOffset || 0}`;
    return this.ctbase.get<Ip>(null, url, undefined, this.xhrOptions);
  }

  userStatus() {
    const body = `user`;
    return this.ctbase.get<{}>(null, body, undefined, this.xhrOptions);
  }
}
