import {Component, OnInit} from '@angular/core';

import {Platform, MenuController} from '@ionic/angular';
import {SplashScreen} from '@ionic-native/splash-screen/ngx';
import {StatusBar} from '@ionic-native/status-bar/ngx';

import {AuthenticationService} from './services/authentication.service';
import {UserService} from './services/user.service';
import {BlockchainService} from './services/blockchain.service';
import {User} from './models/user';
import {Router, RouterEvent, NavigationEnd} from '@angular/router';
import { Blockchain } from './models/blockchain';
import * as moment from 'moment';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss']
})
export class AppComponent implements OnInit {
  userInfo: string;
  blockchainInfo: Blockchain;
  blockchainTime: string;

  public appPages = [
    {
      title: 'Trace',
      url: '/search',
      icon: 'search'
    },
    {
      title: 'Case Manager',
      url: '/cases',
      icon: 'briefcase'
    },
    // {
    //   title: 'Monitoring',
    //   url: '/list',
    //   icon: 'stats'
    // },
    // {
    //   title: 'Wiki',
    //   url: '/list',
    //   icon: 'paper'
    // },
    // {
    //   title: 'Settings',
    //   url: '/settings',
    //   icon: 'settings'
    // },
    {
      title: 'Log out',
      url: './login',
      icon: 'log-out',
      logout: true
    }
  ];

  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    private authenticationService: AuthenticationService,
    private userService: UserService,
    private blockchainService: BlockchainService,
    private menuCtrl: MenuController,
    private router: Router
  ) {
    this.initializeApp();
  }

  ngOnInit() {
    this.router.events.subscribe((event: RouterEvent) => {
      if (event instanceof NavigationEnd && event.url === '/login') {
        this.menuCtrl.enable(false);
      }
    });
  }

  generalStats() {
    // TODO: not sure if these calls belong in this file
    this.userService.getUserData().toPromise().then(res => {
      this.userInfo = `${res.firstName} ${res.lastName}`;
    });

    this.blockchainService.getData().toPromise().then(res => {
      this.blockchainInfo = res;
      this.blockchainTime = moment(res.blockTime * 1000).fromNow()
    })
  }

  initializeApp() {
    this.platform.ready().then(() => {
      this.menuCtrl.enable(false);
      this.splashScreen.hide();

      this.statusBar.styleDefault();

      // Default style doesn't work on Android (ENG-669)
      if(this.platform.is('android')) {
        this.statusBar.backgroundColorByHexString("#ffffff");
      }

      this.authenticationService.authenticationStateReady().then(authstate => {
        authstate.subscribe(state => {
          if (state) {
            this.menuCtrl.enable(true);
            // if(this.router.url == '/login') {
            //   this.router.navigate([this.router.n]);
            // }

            this.generalStats();

            // hit blockchain endpoint every 15 minutes to keep session alive
            var interval = setInterval(() => { this.generalStats() }, 15*60*1000);
          } else {
            this.router.navigate(['login']);
          }
        });
      });

    });
  }

  logout() {
    this.authenticationService.logout();
  }
}
