import {Injectable} from '@angular/core';
import {StoreService} from '../services/store.service';
import {HttpClient, HttpErrorResponse, HttpHeaders} from '@angular/common/http';
import {Observable, throwError, from} from 'rxjs';
import {catchError, retry, switchMap} from 'rxjs/operators';

export const ctUrlMap = {
  'prod': 'https://login.ciphertrace.com',
  'stag': 'https://staging.ciphertrace.com',
  'prev': 'https://preview.ciphertrace.com',
  'dev': 'https://dev-ui-alb.ciphertrace.com',
  'intel': 'https://intel.ciphertrace.com',
  // 'dev': 'http://localhost:8010/proxy',
  // 'dev_new_proxy': 'https://localhost:443',
  'local': 'https://localhost:8443',
  // 'dev': 'http://mobile.ciphertrace.com:8010/proxy'
}; /* ^ dev for dealing with CORS problem (https://www.npmjs.com/package/local-cors-proxy) */

@Injectable({
  providedIn: 'root'
})
export class CtbaseService {

  constructor(private store: StoreService, private http: HttpClient) {
  }

  async getBaseUrl(module: string, path: string, api: string = 'v1'): Promise<string> {

    const env = this.store.getEnv();

    if (api) {
      api = 'api/' + api;
    }

    return [ctUrlMap[await env], api, module, path].filter(v => !!(v)).join('/');
  }

  get<T>(module: string, path: string, api: string,
         options = {headers: {Accept: 'application/json'}}): Observable<T> {

    if (!options['headers']) {
      options.headers = {Accept: 'application/json'};
    }
    return from(this.getBaseUrl(module, path, api)).pipe(
      switchMap((url) => {
        return this.http.get<T>(url, options)
          .pipe(
            retry(3), // retry a failed request up to 3 times
            catchError(this.handleError) // then handle the error
          );
      }));
  }

  post<T>(module: string, path: string, api: string, body: {} | string, options?: {}) : Observable<T> {
            //  options = {headers: {Accept: 'application/json'}, withCredentials: true}

    options = Object.assign({
      headers: {Accept: 'application/json'},
      withCredentials: true,
    }, options);

    // if (!options['headers']) {
    //   options.headers = {Accept: 'application/json'};
    // }
    // options.withCredentials = true;
    return from(this.getBaseUrl(module, path, api)).pipe(
      switchMap((url) => {
        return this.http.post<T>(url, body, options)
          .pipe(
            retry(3), // retry a failed request up to 3 times
            catchError(this.handleError) // then handle the error
          );
      }));
  }

  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }

    // return an observable with a user-facing error message
    // NOTE: important to keep error status and message separated by semicolon,
    // it is used by search controller and others to handle bad requests and anauthorized
    return throwError(`${error.status}: Something bad happened; please try again later.`);
  }
}
