import { Injectable } from '@angular/core';
import { CtbaseService } from '../api/ctbase.service';
import { Observable } from 'rxjs';
import { xhrOpts } from './options';
import { User } from '../models/user';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  private xhrOptions = xhrOpts;

  constructor(
    private ctbase: CtbaseService
  ) { }

  getUserData(): Observable<User> {
    const url = `user`;
    return this.ctbase.get<User>(null, url, undefined, this.xhrOptions);
  }
}
